<script>
  import Vue from 'vue';
  import server from "../../../config/api";
  import common from '../../../libs/common';
  import niceScroll from 'jquery.nicescroll';
  import ImagePreviewModal from '../../../components/image-preview.vue';

  export default {
    components: {
      'ImagePreviewModal': ImagePreviewModal
    },
    data() {
      return {
        taskId: undefined,
        taskDetail: {
          task: {}
        },
        checkList: {
          illegalActions: {}
        },
        common: common,
        disabled: true,
        fileList: [],
        imagePreviewModal: false,
      };
    },
    methods: {
      getReviewResult: function(result) {
        if(result === 'Y') {
          return '';
        } else if(result ===  'N') {
          return '';
        } else {
          return '';
        }
      },
      getStatus: function() {
        if(this.taskDetail.task.status === 'OPEN') {
          return '';
        } else if(this.taskDetail.task.status === 'EXECUTING') {
          return '';
        } else if(this.taskDetail.task.status === 'TOREVIEW') {
          return '';
        } else if(this.taskDetail.task.status === 'DONE') {
          return '';
        } else {
          return '';
        }
      },
      showImgListPreview: function(imgList) {
        this.fileList = imgList;
        this.imagePreviewModal = true;
      },
      getResult: function() {
        if(this.taskDetail.task.result === 'PASS') {
          return '';
        } else if(this.taskDetail.task.result === 'FAIL') {
          return '';
        } else {
          return '';
        }
      },
      closeImagePreviewModal: function() {
        this.imagePreviewModal = false;
      }
    },
    mounted() {
      this.common = common;
      this.taskId = this.$route.query.taskId;
      this.$http.get(server.api.routine.record.detail.replace('{taskId}', this.taskId)).then((response) => {
        let checkList = JSON.parse(response.body.data.task.taskContent);
        response.body.data.task.taskContent = undefined;
        this.taskDetail = response.body.data;
        let status = this.taskDetail.task.status;
        if(status === 'OPEN') {
          status = '';
        } else if(status === 'EXECUTING') {
          status = '';
        } else if(status === 'TOREVIEW') {
          status = '';
        } else if(status === 'DONE') {
          status = '';
        }
        this.taskDetail.task.status = status;

        let result = this.taskDetail.task.result;
        if(result === 'FAIL') {
          result = '';
        } else if(result === 'PASS') {
          result = '';
        } else {
          result = '';
        }
        this.taskDetail.task.result = result;
        checkList.illegalActions = _.sortBy(checkList.illegalActions, 'order');
        for (let index in checkList.illegalActions) {
          checkList.illegalActions[index].items = _.sortBy(checkList.illegalActions[index].items, 'order');
        }
        checkList.issueItems = _.sortBy(checkList.issueItems, 'order');
        for (let index in checkList.issueItems) {
          checkList.issueItems[index].options = _.sortBy(checkList.issueItems[index].options, 'order');
        }

        this.checkList = checkList;
        Vue.nextTick(() => {
          $(".right-container").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "7px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
        });
      }, (error) => {
        this.$Message.error("");
      });

//      this.rectification.illegalActions = _.sortBy(this.rectification.illegalActions, 'order');
//      for(let index in this.rectification.illegalActions) {
//        let items = this.rectification.illegalActions[index].items;
//        items = _.sortBy(items, 'order');
//        this.rectification.illegalActions[index].items = items;
//      }
      // 
//      this.$http.get(server.api.routine.record.detail.replace('{taskId}', this.taskId)).then((response) => {
//        this.checkList = JSON.parse(response.body.data.task.taskContent);
//        response.body.data.task.taskContent = undefined;
//        this.taskDetail = response.body.data;
//        for (let index in this.checkList.checkSection) {
//          let section = this.checkList.checkSection[index];
//          section.items = _.sortBy(section.items, 'order');
//          for (let jndex in section.items) {
//            let item = section.items[jndex];
//            item.options = _.sortBy(item.options, 'order');
//          }
//        }
//      }, (error) => {
//        this.$Message.error("");
//      });
    }

  }
</script>



